import React, { useMemo, useState } from 'react'
import SyntaxHighlighter from 'react-syntax-highlighter';
import { tomorrowNightEighties } from 'react-syntax-highlighter/dist/esm/styles/hljs';


function App() {

  const [URLparams, setURLparams] = useState({
    url: "https://twitter.com/9GAG/status/1386304742704525317",
    selector: "[data-testid='tweet']"
  })

  const [data, setData] = useState({})

  const codeString = useMemo(() => {
    return `
    fetch("http://localhost:3000/api/?${Object.entries(URLparams).map(([key, val]) => `${key}=${val}`).join('&')
      }", requestOptions)
    .then(response => response.json())
    .then(result => console.log(result))
    .catch(error => console.log('error', error));`
  }, [URLparams])

  const handleSubmit = async (e) => {
    e.preventDefault();
    const item = await fetch("/api/?url=https://twitter.com/9GAG/status/1386304742704525317&selector=[data-testid='tweet']")
    const data = await item.json();
    setData(data)
  }

  return (
    <div className="container mx-auto">
      <div className="grid grid-cols-editor gap-3">
        <form onSubmit={handleSubmit} class="space-y-4 text-gray-700">
          <div class="flex flex-wrap">
            <div class="w-full">
              <label class="block mb-1" for="formGridCode_card">URL*</label>
              <input required class="w-full h-10 px-3 text-base placeholder-gray-600 border rounded-lg focus:shadow-outline" type="text" />
            </div>
          </div>
          <div class="flex flex-wrap">
            <div class="w-full">
              <label class="block mb-1" for="formGridCode_card">Selector*</label>
              <input required class="w-full h-10 px-3 text-base placeholder-gray-600 border rounded-lg focus:shadow-outline" type="text" />
            </div>
          </div>
          <div class="flex flex-wrap">
            <div class="w-full">
              <label class="block mb-1" for="formGridCode_card">Format</label>
              <input placeholder="Default format: PNG" class="w-full h-10 px-3 text-base placeholder-gray-600 border rounded-lg focus:shadow-outline" type="text" />
            </div>
          </div>
          <div class="flex flex-wrap -mx-2 space-y-4 md:space-y-0">
            <div class="w-full px-2 md:w-1/3">
              <label class="block mb-1" for="formGridCode_month">Width</label>
              <input class="w-full h-10 px-3 text-base placeholder-gray-600 border rounded-lg focus:shadow-outline" type="text" id="formGridCode_month" />
            </div>
            <div class="w-full px-2 md:w-1/3">
              <label class="block mb-1" for="formGridCode_year">Height</label>
              <input class="w-full h-10 px-3 text-base placeholder-gray-600 border rounded-lg focus:shadow-outline" type="text" id="formGridCode_year" />
            </div>
            <div class="w-full px-2 md:w-1/3">
              <label class="block mb-1" for="formGridCode_year">Padding </label>
              <input class="w-full h-10 px-3 text-base placeholder-gray-600 border rounded-lg focus:shadow-outline" type="text" id="formGridCode_year" />
            </div>
          </div>
          <div class="flex flex-wrap">
            <div class="w-full">
              <label class="text-gray-700 flex items-center">
                <input type="checkbox" value="" />
                <span class="ml-1">Cloud Upload</span>
              </label>
            </div>
            <input value="Let's try" type="submit" class="h-10 px-5 my-2 text-indigo-100 transition-colors duration-150 bg-indigo-700 rounded-lg focus:shadow-outline hover:bg-indigo-800"></input>
          </div>
        </form>
        <SyntaxHighlighter
          language="javascript" style={tomorrowNightEighties}>
          {codeString}
        </SyntaxHighlighter>
        <SyntaxHighlighter
          language="json" style={tomorrowNightEighties}>
          {JSON.stringify(data, null, 4)}
        </SyntaxHighlighter>
      </div>
      <img src={data.data}></img>
    </div>
  );
}

export default App;
